import { Component, h, Prop, Element, Build, Host } from '@stencil/core';
import { common } from '../../../global/data/common';
import { ListingFilterData, ListingFilterCheckboxData, ListingFilterSliderData } from '../../../global/data/listing';

@Component({
    tag: 'ks-filtering',
    styleUrl: 'filtering.css'
})
export class Filtering {
    @Element() root: HTMLElement;
    @Prop() baseUrl: string;
    @Prop() query: string;
    @Prop() filters: ListingFilterData[];

    submit(e: Event) {
        e.preventDefault();
        const inputs = this.root.querySelectorAll("form input") as NodeListOf<HTMLInputElement>;

        let data = new Object();

        inputs.forEach(input => {
            if(input.value == "" || input.name == "") return;

            if(input.name in data)
                data[input.name] += `-${input.value}`;
            else data[input.name] = input.value;
        });

        let url = this.baseUrl;
        for (const name in data) {
            url += `/${name}=${data[name]}`;
        }

        window.location.href = url;
        return false;
    }

    render() {
        const strings = common.get('translations');

        if (!Build.isBrowser)
            return <Host/>;

        

        return[
            <ks-button narrow muted border name={strings.filter} left icon="filter"
                onClick={()=>this.root.querySelector('ks-sidepanel').show()}
            />,
            <ks-sidepanel left>
                <span class="heading">{strings.filteringHeader}</span>

                <form method="POST" action={this.baseUrl}>
                    <input type="hidden" name="postget" value="tak" />
                    
                    {this.query ? 
                        <input type="hidden" name="szukaj" value={this.query} />
                    : null}

                    {this.filters.map(filter => filter?.items.length > 0 ?
                        <ks-filter name={filter.name} active={filter.active}>

                            {filter.items.map(item => {
                                if(filter.type == "checkbox") {
                                    item = item as ListingFilterCheckboxData;
                                    return <ks-filter-checkbox active={item.active} name={item.name} value={item.value} text={item.content} />
                                }

                                if(filter.type == "color") {
                                    item = item as ListingFilterCheckboxData;
                                    return <ks-filter-color active={item.active} name={item.name} value={item.value} color={item.content} />
                                }
                                
                                if(filter.type == "slider") {
                                    item = item as ListingFilterSliderData;
                                    return <ks-filter-slider name={item.name} values={item.values} snap={item.snap}
                                                steps={item.steps} from={item.from} to={item.to} />;
                                }

                                return null;
                            })}

                        </ks-filter>
                    : null)}

                    <ks-button class="clear" border link={this.baseUrl} name={strings.filteringClear}/>
                    <ks-button submit secondary name={strings.filteringSubmit}/>
                </form>
            </ks-sidepanel>
        ];
    }
}