import { Component, h, Prop } from '@stencil/core';

@Component({
    tag: 'ks-listing-footer',
    styleUrl: 'listing-footer.css'
})
export class ListingFooter {
    @Prop() description: string;

    render() {
        return[
            this.description ? <div innerHTML={this.description}/> : null,
            <slot name="tags"/>,
            <slot name="comments"/>
        ];
    }
}