import { Component, h, Prop, Listen, Element } from '@stencil/core';
import { common } from '../../../global/data/common';

@Component({
    tag: 'ks-sorting',
    styleUrl: 'sorting.css'
})
export class Sorting {

    @Element() root: HTMLElement;
    @Prop() post: string;
    @Prop() current: number;

    @Prop({reflect: true, mutable: true}) enabled: boolean = false;

    render() {
        const strings = common.get('translations');

        return(
            <div>
                <form action={this.post} method="post" style={{margin: "0"}}>
                    <input type="hidden" name="sortowanie" value="0"></input>
                </form>

                <ks-button onClick={()=>this.toggle()} narrow muted border name={strings.sorting} icon="shuffle"/>
                <div class="dropdown">
                    <a class={this.current == 1 ? "active" : null} onClick={()=> this.sort(1)}>{strings.sortingRecommended} <ks-icon name="check" size={0.9}/></a>
                    <a class={this.current == 2 ? "active" : null} onClick={()=> this.sort(2)}>{strings.sortingPopular} <ks-icon name="users" size={0.9}/></a>
                    <a class={this.current == 3 ? "active" : null} onClick={()=> this.sort(3)}>{strings.sortingNewest} <ks-icon name="clock" size={0.9}/></a>
                    <hr/>
                    <a class={this.current == 4 ? "active" : null} onClick={()=> this.sort(4)}>{strings.sortingCheapest} <ks-icon name="plus-circle" size={0.9}/></a>
                    <a class={this.current == 5 ? "active" : null} onClick={()=> this.sort(5)}>{strings.sortingExpensive} <ks-icon name="minus-circle" size={0.9}/></a>
                </div>
            </div>
        );
    }

    form: HTMLFormElement;
    input: HTMLInputElement;

    componentDidLoad() {
        this.form = this.root.querySelector("form");
        this.input = this.form.querySelector("input");
    }

    sort(value: number){
        this.input.value = value.toString();
        this.form.submit();
    }

    toggle(){
        this.enabled = !this.enabled;
    }

    @Listen("click", {target: "window"})
    disable(e: Event){
        if(!this.root.contains(e.target as HTMLElement))
            this.enabled = false;
    }
}